@import url('https://fonts.cdnfonts.com/css/dm-serif-text');

body {
    font-family: 'Times New Roman', Times, serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.journal-container {
    background-color: white;
    padding: 20px 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 700px;
    width: 100%;
    margin: 20px;
}

.journal-title {
    font-family: 'DM Serif Text', serif;
    font-size: 2.5em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-premise {
    font-size: 1.2em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-author,
.journal-date {
    font-size: 1em;
    text-align: start;
    /* margin: 5px 0; */
    color: #999;
}

.journal-date {
    margin-bottom: 5px;
}

.journal-content {
    font-size: 1em;
    line-height: 1.6;
    margin-bottom: 20px;
}
